.main{
  border:3px solid black;
  display:flex;
  justify-content:center;
}
.box{
  max-width: 500px;
  display: flex;
  // border:2px solid red;
  margin:10px;
  flex-direction:column;
  align-items: center;
  img{
    width: 80%;
  }
}
button{
  background: #f7f8aa;
  padding: 10px 20px;
  border-radius: 3px;
  margin:4px;
  border: 1px solid red;
  cursor: pointer;
  &:hover{
    background: bisque;
    border: 1px solid #000;
  }
}

.tie{border:5px solid gray;color:gray;}
.lose{border:5px solid red;color:#fff;background:red;@debug fade-out(rgba(#e1d7d2,0.5),0.4);
  img{
    // background:fade-out(rgba(#000000,0.5),0.2);
    filter:opacity(50%)
  }
}
.win{border:5px solid skyblue;color:#fff;background: aquamarine;}

.resultp{
  font-size:50px;
  font-weight: 700;
  text-transform:uppercase;//대문자
  border:0;
}

